@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header-icon {
    height: 225px;
}

.header .welcome-text {
    margin-right: 100px;
    font-weight: bold;
    color: #000;
}

.header .welcome-text-loggedin {
    margin-right: 10px;
    font-weight: bold;
    color: #000;
}

.header .dropdown-toggle {
    margin-right: 100px;
}

.intro-learner, .intro-master {
    width: 100vw;
    display: flex;
    align-items: center;
}

.intro-learner {
    height: 80vh;
}

.intro-master {
    height: 100vh;
}

.content, .illus {
    width: 50%;
    text-align: center;
}

.illus .illus-img {
    width: 80%;
}

.content {
    padding: 80px;
}

.content p {
    text-align: justify;
    margin-bottom: 50px;
    font-size: 20px;
}

.btn-start {
    background-color: #1A535C;
    border: none;
    color: #FFF;
    font-weight: bold;
    font-size: 20px;
    padding: 15px 25px;
}

.mobile-store {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.mobile-store-icons {
    display: flex;
    align-items: center;
}

.mobile-store-icons .googleplaystore-icon {
    height: 120px;
}

.mobile-store-icons .appstore-icon {
    margin-right: 100px;
    height: 80px;
}

.header a {
    text-decoration: none;
    color: inherit;
}

.dropdown-menu {
    transform: translate3d(0px, 50px, 0px) !important;
    border-radius: 0;
    text-align: center;
}

.dropdown-item:active {
    background-color: gray;
}

nav {
    width: 25% !important;
    background-color: #FFF;
}

.learner-dashboard {
    height: 100vh;
    overflow: auto;
    width: 100vw;
    background-color: #fffffc;
}

.icon img {
    width: 100%;
    min-width: 200px;
    border-bottom: 8px #1A535C solid;
}

.user img {
    width: 125px;
}

.course-card-deck {
    overflow-x: auto;
    flex-wrap: nowrap;
}

.course-card {
    min-width: 350px;
    max-width: 350px;
    border-radius: 7%;
}

.course-card-img-top {
    height: 350px;
    width: 100%;
    object-fit: cover;
    border-top-left-radius: 7%;
    border-top-right-radius: 7%;
}

.result-img {
    height: 50px;
    width: 50px;
    object-fit: cover;
}

.learner-dashboard .search-bar {
    height: 10%;
}

.learner-dashboard .courses {
    height: 90%;
}
.master-dashboard {
    height: 100vh;
    overflow: auto;
    width: 100vw;
    background-color: #fffffc;
}

.master-dashboard .card {
    color: #000;
    width: 90%;
    height: 350px;
    margin: 0;
    padding: 0;
}

.master-card img {
    height: 100%;
    width: 350px;
    object-fit: cover;
}

.master-dashboard .btn-add-course {
    background-color: #1A535C;
    border-radius: 0;
    color: white;
    font-weight: bold;
    font-size: 20px;
    padding: 15px 25px;
    border: none;
}
.course-dashboard {
    height: 100vh;
    overflow: auto;
    width: 100vw;
    background-color: #fffffc;
}

.course-dashboard .course-header {
    height: 40%;
    width: 100%;
    background-image: url("https://picsum.photos/4000/1500");
}

.course-dashboard .course-title, .course-dashboard .master-info-name {
    mix-blend-mode: difference;
    color: white;
}

.course-dashboard .course-description {
    max-height: 25%;
}

.course-dashboard .profile-pic {
    width: 100px;
    height: 100px;
    border-radius: 50%;
}

.course-dashboard .course-learners {
    width: 30%;
    max-height: 575px;
    overflow: auto;
}

.course-dashboard .course-btns {
    margin-left: 40px;
}

.course-dashboard .course-register-btn {
    background-color: #fffffc;
    border-radius: 0;
    font-weight: bold;
}

.course-dashboard .course-unregister-btn {
    background-color: gray;
    color: white;
    border-radius: 0;
    font-weight: bold;
}

.course-dashboard .course-archive-btn {
    background-color: #ff6b6b;
    color: white;
    border-radius: 0;
    font-weight: bold;
}

.course-dashboard .user-myself {
    background-color: #F7FFF7;
}

.course-lectures .btn-toggle-lecture {
    text-decoration: none;
    color: inherit;
}

.course-lectures .btn-lecture-icon {
    background-color: #1A535C;
    border-radius: 0;
    color: white;
}

.course-lectures .btn-replay-icon {
    background-color: #2EC4B6;
    border-radius: 0;
    color: white;
}

.course-lectures .btn-delete-lecture {
    background-color: #ff6b6b;
    border-radius: 0;
    border: none;
    color: white;
}

.course-lectures .btn-edit-lecture {
    background-color: #2EC4B6;
    border-radius: 0;
    border: none;
    color: white;
}

.btn-delete-course {
    background-color: #ff6b6b;
    border-radius: 0;
    border: none;
    color: white;
}

.course-container {
    min-height: 60%;
}
.profile-dashboard {
    height: 100vh;
    overflow: auto;
    width: 100vw;
    background-color: #fffffc;
}

.profile-dashboard .profile-header {
    height: 35%;
}

.profile-dashboard .day-counter {
    background-color: #2EC4B6;
    min-height: 80px;
    min-width: 80px;
    border-radius: 50%;
}
html,
body {
	width: 100%;
	height: 100%;
	margin: 0;
	padding: 0;
	font-family: 'Open Sans', sans-serif;
}

.container {
	height: 100%;
	width: 100%;
	margin: 0;
	padding: 0;
}

h1, h2, h3, h4, h5, h6, p, a, button {
	margin: 0;
	padding: 0;
}

a, a:hover, a:active {
	text-decoration: none;
	color: inherit;
}

.alert {
	z-index: 1000 !important;
}
